<template>
  <div id="out-main" class="layui-bg-gray">
    <div id="top"></div>
    <navigationComponent></navigationComponent>
    <router-view></router-view>
    <div id="go-top-btn" ref="goTop" @click="returnTop"><i class="layui-icon layui-icon-top"></i>
    </div>
  </div>
</template>

<script>
import navigationComponent from './components/navigationComponent.vue'
import page1Component from './components/page1Component.vue'
import resumeComponent from './components/resumeComponent.vue'
export default {
  name: 'App',
  components: {
    navigationComponent,
    page1Component,
    resumeComponent
  },
  mounted () {
    window.addEventListener('scroll', this.windowScroll)
  },
  methods: {
    windowScroll () { // 返回顶部按钮动画
      // 滚动条距离页面顶部的距离
      let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
      var goTop = this.$refs.goTop.style// 获取节点
      if (scrollTop > 200) { // 判断距离是否触发
        goTop.animationName = 'up'
        goTop.animationDuration = '.4s'
        goTop.bottom = '40px'
      } else if (scrollTop < 200) {
        goTop.animationName = 'down'
        goTop.animationDuration = '.4s'
        goTop.bottom = '-40px'
      }
    },
    returnTop: function () { // 返回顶部按钮
      window.scrollTo({
        top: 0,
        behavior: 'smooth'
      })
    }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
* {
  box-sizing: border-box;
}
#out-main {
  min-height: 100vh;
}
.container {
  position: relative;
  margin: 0 auto;
  padding: 50px 0;
  width: 800px;
  text-align: center;
}
.gray-left-line {
  position: relative;
  margin: 20px auto;
  padding-left: 20px;
  width: 80%;
  line-height: 24px;
  text-align: left;
  border-left: 3px solid #C0C4CC;
}
#go-top-btn {
  position: fixed;
  bottom: -40px;
  right: 80px;
  width: 25px;
  height: 25px;
  border-radius: 6px;
  background-color: #C2C2C2;
  line-height: 25px;
  text-align: center;
  cursor: pointer;
}
@keyframes up {
  from {
  bottom: -40px;
  }
  to {
  bottom: 40px;
  }
}
@keyframes down {
  from {
  bottom: 40px;
  }
  to {
  bottom: -40px;
  }
}
</style>



