<template>
  <div>
    <div class="h-inner">
      <div class="brand">
        <div class="layui-circle">
          <img src="../assets/fox.jpg">
        </div>
      </div>
      <div id="slogan">
        <p>枪在手，跟我走，前端路上不回头</p>
      </div>
      <div id="h-nav">
        <ul class="layui-bg-gray layui-nav" lay-filter="">
          <router-link to="/" class="router-class layui-nav-item">
            <li class="layui-nav-item layui-anim nav-btn">
              <i class="layui-icon">&#xe68e;</i><br>
              <p class="layui-inline">前端博客</p>
            </li>
          </router-link>
          <router-link to="page2" class="router-class layui-nav-item">
            <li class="layui-nav-item layui-anim nav-btn">
              <i class="layui-icon">&#xe66e;</i><br>
              <p class="layui-inline">成长之路</p>
            </li>
          </router-link>
          <router-link to="works" class="router-class layui-nav-item">
            <li class="layui-nav-item layui-anim nav-btn">
              <i class="layui-icon">&#xe64e;</i><br>
              <p class="layui-inline">前端作品</p>
            </li>
          </router-link>
          <router-link to="resume" class="router-class layui-nav-item">
            <li class="layui-nav-item layui-anim nav-btn">
              <i class="layui-icon">&#xe600;</i><br>
              <p class="layui-inline">我的简历</p>
            </li>
          </router-link>
          <!-- <router-link to="/" class="router-class layui-nav-item">
            <li class="layui-nav-item layui-anim nav-btn">
              <i class="layui-icon">&#xe7ae;</i><br>
              <p class="layui-inline">{{message}}</p>
            </li>
          </router-link> -->
        </ul>
      </div>
    </div>
  </div>
</template>

<script src="../layui/layui.js"></script>
<script>
export default {
  name: 'Navigation',
  data () {
    return {
      message: 'First page!'
    }
  }
}
</script>

<style src="../layui/css/layui.css"></style>
<style scoped>
.h-inner {
  position: relative;
  margin: 0 auto;
  padding: 100px 0 70px;
  width: 100%;
  text-align: center;
}
.brand {
  margin: 0 auto;
}
img {
  width: 80px;
  height: 80px;
  border-radius: 40px;
}
.theme {
  display: inline-block;
  width: 160px;
  height: 36px;
  font-size: 20px;
  color: #fff;
  line-height: 36px;
  background-color: #222;
}
#slogan {
  margin-top: 10px;
  font-size: 13px;
  color: #999;
}

.router-class {
  margin: 0 15px;
  line-height: 22px;
}

#h-nav {
  margin-top: 35px;
  text-align: center;
}

#h-nav li {
  line-height: 22px;
}

.nav-btn {
  display: inline-block;
  position: relative;
  z-index: 10;
  cursor: pointer;
  -webkit-animation-name: lost-btn;
  -webkit-animation-duration: .2s;
}

.nav-btn:hover {
  position: relative;
  bottom: 7px;
  -webkit-animation-name: get-btn;
  -webkit-animation-duration: .2s;
}

@keyframes get-btn {
  from {
  bottom: 0;
  }

  to {
  bottom: 7px;
  }
}

@keyframes lost-btn {
  from {
  bottom: 7px;
  }

  to {
  bottom: 0;
  }
}
</style>



